import { container,title } from "assets/jss/material-kit-react.js";

const productStyle = {
  container: {
    zIndex: "12",
    color: "#FFFFFF",
    ...container
  },
  section: {
    padding: "5px 0",
    textAlign: "center"
  },
  title: {
    ...title,
    marginBottom: "1rem",
    marginTop: "30px",
    minHeight: "32px",
    textDecoration: "none",
    textAlign:"left",
    cursor:'pointer'
  },
  description: {
    color: "#999"
  },
  spanalign:{
    display:"flex"
  }
};

export default productStyle;
