import React from "react";
import { useParams } from "react-router-dom";
//import { useParams } from "react-router-dom";
import useAxios from 'axios-hooks';
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// core components
import Card from "components/Card/Card";
import CardHeader from 'components/Card/CardHeader';
import CardBody from "components/Card/CardBody";
import CardFooter from "components/Card/CardFooter";
import Header from "components/Header/Header.js";
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button";

import {MapContainer,TileLayer,Marker,Popup} from 'react-leaflet';
import HeaderLinks from "components/Header/ChildHeaderLinks";
import moment from "moment";
import 'moment/locale/es';
// core components
import Parallax from "components/Parallax/Parallax.js";
import CardMedia from '@material-ui/core/CardMedia';
//ALERT
import SnackbarContent from "components/Snackbar/SnackbarContent.js";
import Skeleton from "react-loading-skeleton";
import GLOBAL from 'global';

// styles
import { container, title,  } from "assets/jss/material-kit-react.js";
import myStyles from "assets/jss/material-kit-react/views/myStyles";
import 'assets/css/contactoView/contactoView.css';
import logoVertical from "assets/img/logo-horizontal.png";

const dashboardRoutes = [];
moment.locale('es');
const styles = {
  container,
  title:{
    ...title,
    color:'white',
    textAlign:'center',
    width:'100%'
  },
  titleData:{
    ...title,    
    textAlign:'center',
    width:'100%',
    borderRadius: '25px',
    background:'#8fc9e8'
  }, 
  ...myStyles,
  justifyCenter: {
    justifyContent: "center !important"
  },  
  inputRootCustomClasses: {
    margin: "0!important",        
  },
  formControl: {
    margin: "0 !important",
    paddingTop: "0",
    width:"80%"
  },
  searchIcon: {
    width: "20px",
    height: "20px",
    color: "inherit"
  },
  dividerStyle:{
    border:'solid 1px #e9e9e9'
  },
  customImage:{    
    height: 0,
    paddingTop: '56.25%', // 16:9
},
}

const useStyles = makeStyles(styles);

export default function ChildEventoDetalleView(props) {
    const classes = useStyles();
    const { slug } = useParams();
    const { ...rest } = props;
    const [{ data, loading, error }, refetch] = useAxios(
        `${GLOBAL.apiURL}/api/unidad/datosUbicacion/${slug}`
    );
    const getPosition = () => {
        let posxy = [51.505, -0.09];
        if(data.data && data.data.latitud && data.data.longitud){
            posxy = [data.data.latitud,data.data.longitud];
        }
        return posxy;
    }
    React.useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }, []); 
  
    return (
        <div>
            <Header
                color="transparent"
                routes={dashboardRoutes}
                brand={<img src={logoVertical} className={classes.logoImg} alt="Logo" />}
                rightLinks={<HeaderLinks />}
                fixed
                changeColorOnScroll={{
                height: 400,
                color: "primary"
                }}
                {...rest}
            />
            <Parallax small className={classes.customParalaxBg}>
                <h1 className={classes.title}>UBICACIONES<br/><small style={{color:'white'}}>{data && data.status?String(data.message).toUpperCase():''}</small></h1>
            </Parallax>
            <div className={classNames(classes.main, classes.mainRaised)}>
                <div className={classes.container}>            
                    <div className={classes.section}>                        
                        <GridContainer>
                            <GridItem xs={12}>
                                {
                                    !loading && error || (data && !data.status)?
                                    <SnackbarContent
                                        message={<div><span><b>ERROR:</b> Se produjo un error en la obtención de datos.</span><Button color="light" simple><i className={" fab fa-refresh"}/></Button></div>}
                                        close
                                        color="danger"
                                        icon="info_outline"
                                    />:null
                                }                                      
                            </GridItem>              
                        </GridContainer>                   
                        <GridContainer justify='center'>
                            {
                                loading?
                                <GridItem xs={12}>
                                    <Skeleton height='320px'/>
                                </GridItem>:null
                            }
                            {
                                !loading && data && data.status && data.data?
                                <GridItem xs={12} sm={12}>
                                    <Card>
                                        <CardHeader>
                                            <h4 className={classes.titleData}>{String(data.data.nombreunidad).toUpperCase()}</h4>
                                        </CardHeader>                                                                    
                                        <CardBody>
                                            <GridContainer>
                                                <GridItem xs={12} sm={4} md={4}>
                                                    <strong>LUGAR</strong>
                                                </GridItem>
                                                <GridItem xs={12} sm={8} md={8}>
                                                    <p>{data.data.lugar}</p>
                                                </GridItem>
                                            </GridContainer>
                                            <GridContainer>
                                                <GridItem xs={12} sm={4} md={4}>
                                                    <strong>DIRECCIÓN</strong>
                                                </GridItem>
                                                <GridItem xs={12} sm={8} md={8}>
                                                    <p>{data.data.direccion}</p>
                                                </GridItem>
                                            </GridContainer>                                                                                     
                                            <div>
                                                <strong>IMAGEN DE LA DIRECCIÓN</strong>
                                                <CardMedia
                                                    className={classes.customImage}
                                                    image={data.data.imagen}
                                                />   
                                            </div>
                                            <div>
                                                <strong>UBICACIÓN</strong>
                                                <MapContainer center={getPosition()} zoom={13} scrollWheelZoom={false}>
                                                    <TileLayer
                                                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                                                    />
                                                    <Marker position={getPosition()}>
                                                        <Popup>
                                                            {data.data && data.data.nombreunidad?data.data.nombreunidad:'EVENTO'}
                                                        </Popup>
                                                    </Marker>
                                                </MapContainer>
                                            </div>                                           
                                        </CardBody>
                                        {
                                            false?                                     
                                            <CardFooter>
                                                <Button color="twitter">
                                                    <i
                                                    className={"fab fa-twitter"}
                                                    />{" "}
                                                    Compartir con Twitter
                                                </Button>
                                                <Button color="facebook">
                                                    <i
                                                    className={"fab fa-facebook"}
                                                    />{" "}
                                                    Compartir con Facebook
                                                </Button>
                                            </CardFooter>:null
                                        }
                                    </Card>
                                </GridItem>:null
                            }
                        </GridContainer>
                    </div>        
                </div>
            <Footer />
            </div>
        </div>
    );
}
